import "./App.css";
import React, { useState, useEffect } from "react";
import Main_Content from "./Components/Main_Content/Main_Content";
import Loader from "./Loader";

function App() {
  const [loader, setloader] = useState(true);
  useEffect(() => {
    setTimeout(() => setloader(false), 2000);
  }, []);

  return (
    <>
      {loader && <Loader />}
      <Main_Content />
    </>
  );
}

export default App;
